.MainPageButton{
    text-transform: uppercase;
    transition: .3s ease-in;
    width: 80px;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    padding: 15px;
    font-size: 13px;
}
.MainPageButton:hover::before{
    transition: .3s ease-in;
    width: 100px;
    height: 100px;
    content: '';
    position: absolute;
    border-radius: 50%;
    border: .5px solid #fff;
}
.MainPageButton:hover{
    transition: .3s ease-in;
    background-color: rgba(255, 255, 255, 0.12);
}